import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step16(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please fill out the field" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step
      heading={`We'd like to verify a phone number before completing your application`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow center>
                <MDBCol>
                  <MDBInput
                    label="Phone"
                    group
                    type="tel"
                    name="phone"
                    value={state.phone}
                    onChange={(e) => {
                      if (e.target.value.length > 14) return;
                      onChangeField(
                        e,
                        e.target.name,
                        normalizeInput(e.target.value, state.phone)
                      );
                      // formRef.current.phone.value = normalizeInput(state.phone);
                      validate();
                    }}
                    pattern="\(\d{3}\) \d{3}-\d{4}"
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="z-depth-1a"
                    onClick={() => onChangeField("e", "otpverify", true)}
                  >
                    Send OTP
                  </MDBBtn>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Send OTP
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>

          {state.otpverify && (
            <>
              <MDBRow center md={6}>
                <MDBCol md={6}>
                  <MDBInput
                    label="otp"
                    group
                    type="tel"
                    name="otp"
                    value={state.otp}
                    onChange={(e) => {
                      if (e.target.value.length > 14) return;
                      onChangeField(e, e.target.name, e.target.value);
                      // formRef.current.phone.value = normalizeInput(state.phone);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {state.otp.length && (
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="z-depth-1a"
                    onClick={() => {
                      onChangeField("e", "otpverify", true);
                      history.push(nextStep);
                    }}
                  >
                    Verify
                  </MDBBtn>
                )}
                {!state.otp.length && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Verify
                  </MDBBtn>
                )}
              </MDBRow>
            </>
          )}
        </form>
      </MDBContainer>
    </Step>
  );
}

const normalizeInput = (value, previousValue) => {
  if (!value) return value;
  const currentValue = value.replace(/[^\d]/g, "");
  if (!previousValue || value.length > previousValue.length) {
    if (currentValue.length <= 3) return currentValue;
    if (currentValue.length === 3) return `(${currentValue})`;
    if (currentValue.length <= 6)
      return `(${currentValue.slice(0, 3)}) ${currentValue.slice(3)}`;
    if (currentValue.length === 6)
      return `(${currentValue.slice(0, 3)}) ${currentValue.slice(3)}-`;
    return `(${currentValue.slice(0, 3)}) ${currentValue.slice(
      3,
      6
    )}-${currentValue.slice(6, 10)}`;
  }
};
