import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step12(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please fill out the field" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={`Statistics regarding your transactions`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <h5 className="mt-4 mb-0">Monthly Transaction Volume</h5>
              <MDBRow center>
                <MDBCol>
                  <MDBInput
                    label="Debit"
                    group
                    type="text"
                    name="MTVdebit"
                    value={state.MTVdebit}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                      validate();
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol>
                  <MDBInput
                    label="Credit"
                    group
                    type="text"
                    name="MTVcredit"
                    value={state.MTVcredit}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                      validate();
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <h5 className="mt-4 mb-0">Maximum Daily Transactions</h5>
              <MDBRow>
                <MDBCol md={6}>
                  <MDBInput
                    label="Debit"
                    group
                    type="text"
                    name="MDTdebit"
                    value={state.MDTdebit}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                      validate();
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={6}>
                  <MDBInput
                    label="Credit"
                    group
                    type="text"
                    name="MDTcredit"
                    value={state.MDTcredit}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                      validate();
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <h5 className="mt-4 mb-0">Maximum Transaction Totals</h5>
              <MDBRow>
                <MDBCol md={6}>
                  <MDBInput
                    label="Debit"
                    group
                    type="text"
                    name="MTTdebit"
                    value={state.MTTdebit}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                      validate();
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={6}>
                  <MDBInput
                    label="Credit"
                    group
                    type="text"
                    name="MTTcredit"
                    value={state.MTTcredit}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                      validate();
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol>
                  <MDBInput
                    label="maximum single Transaction"
                    group
                    type="text"
                    name="maximumsingletransaction"
                    value={state.maximumsingletransaction}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                      validate();
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol>
                  <MDBInput
                    label="number of returns estimated / month"
                    group
                    type="text"
                    name="returnspermonth"
                    value={state.returnspermonth}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                      validate();
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
