import React from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step4(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;

  return (
    <Step heading={`Which Type of transactions do you generally run?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "paymentMethod", "Corporate Clients");
                    history.push(nextStep);
                  }}
                >
                  Corporate Clients
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "paymentMethod", "Consumers");
                    history.push(nextStep);
                  }}
                >
                  Consumers
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={5}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(
                      e,
                      "paymentMethod",
                      "Both (Consumers and Corporate)"
                    );
                    history.push(nextStep);
                  }}
                >
                  Both (Consumers and Corporate)
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
