import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step9(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please enter valid email address" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step
      heading={`We just need to know the banking information of where to deposit your funds and collect our fees`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <h3></h3>
              <MDBRow center>
                <MDBCol>
                  <select
                    class="browser-default custom-select"
                    name="accountType1"
                    onChange={(e) =>
                      onChangeField("e", e.target.name, e.target.value)
                    }
                  >
                    <option selected disabled>
                      Account Type
                    </option>
                    <option value="Business">Business</option>
                    <option value="Personal">Personal</option>
                  </select>
                </MDBCol>
                <MDBCol>
                  <select
                    class="browser-default custom-select"
                    name="accountType2"
                    onChange={(e) =>
                      onChangeField("e", e.target.name, e.target.value)
                    }
                  >
                    <option selected disabled>
                      Account Type
                    </option>
                    <option value="Business">Checking</option>
                    <option value="Personal">Savings</option>
                  </select>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol>
                  <MDBInput
                    label="Account Number"
                    group
                    type="tel"
                    name="accountNumber"
                    value={state.accountNumber}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                  <MDBCol>
                    <MDBInput
                      label="Routing Number"
                      group
                      type="tel"
                      name="routingNumber"
                      value={state.routingNumber}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      required
                      containerClass="mb-0"
                    />
                  </MDBCol>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
