import React, { useCallback } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import { usePlaidLink } from "react-plaid-link";

const App = ({ nextStep, prevStep }) => {
  const onSuccess = useCallback(
    (token, metadata) => console.log("onSuccess", token, metadata),
    []
  );

  const onEvent = useCallback(
    (eventName, metadata) => console.log("onEvent", eventName, metadata),
    []
  );

  const onExit = useCallback(
    (err, metadata) => console.log("onExit", err, metadata),
    []
  );

  const config = {
    token: "jnnsbivubsib",
    onSuccess,
    onEvent,
    onExit,
    //  optional parameters
    // receivedRedirectUri: props.receivedRedirectUri || null,
    // ...
  };

  const { open, ready, error } = usePlaidLink(config);

  return (
    <>
      <button
        type="button"
        className="button"
        onClick={() => open()}
        disabled={!ready || error}
      >
        Open Plaid Link
      </button>
      <Link to={nextStep}>
        <MDBBtn color="pink" rounded type="button" className="z-depth-1a">
          Next
        </MDBBtn>
      </Link>
    </>
  );
};

export default App;
