import React from "react";
import { Link } from "react-router-dom";

import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
export default class ImageUpload extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      file: "",
      imagePreviewUrl: "",
      prevStep: props.prevStep,
      nextStep: props.nextStep,
      history: props.history,
    };
  }

  _handleSubmit(e) {
    e.preventDefault();
    // TODO: do something with -> this.state.file
    console.log("handle uploading-", this.state.file);
  }

  _handleImageChange(e) {
    e.preventDefault();

    let reader = new FileReader();
    let file = e.target.files[0];

    reader.onloadend = () => {
      this.setState({
        file: file,
        imagePreviewUrl: reader.result,
      });
    };

    reader.readAsDataURL(file);
  }

  render() {
    let { imagePreviewUrl } = this.state;
    let $imagePreview = null;
    if (imagePreviewUrl) {
      $imagePreview = (
        <img
          style={{ height: "250px", width: "250px" }}
          src={imagePreviewUrl}
        />
      );
    } else {
      $imagePreview = (
        <div className="previewText">Please Upload your driving license</div>
      );
    }

    return (
      <Step heading={`We'd like to verify your drivers license`}>
        <img
          src={BackBtn}
          className="btn-back"
          onClick={() => this.state.history.push(this.state.prevStep)}
        />
        <MDBContainer>
          <MDBRow center className="previewComponent">
            <form onSubmit={(e) => this._handleSubmit(e)}>
              <MDBRow center className="mt-3">
                <MDBCol>
                  <div className="imgPreview">{$imagePreview}</div>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="mt-5">
                <MDBCol>
                  <input
                    className="fileInput"
                    type="file"
                    onChange={(e) => this._handleImageChange(e)}
                  />
                </MDBCol>
              </MDBRow>
            </form>
          </MDBRow>
          <MDBRow center className="d-flex flex-column align-items-center mt-3">
            {this.state.file && (
              <Link to={this.state.nextStep}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="z-depth-1a"
                  onClick={(e) => {
                    this._handleSubmit(e);
                    this.state.history.push(this.state.nextStep);
                  }}
                >
                  Next
                </MDBBtn>
              </Link>
            )}
            {!this.state.file && (
              <MDBBtn
                color={"grey"}
                disabled
                rounded
                type="button"
                className="z-depth-1a"
              >
                Next
              </MDBBtn>
            )}
          </MDBRow>
        </MDBContainer>
      </Step>
    );
  }
}
