import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step13(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [value, setvalue] = useState(0);
  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please fill out the field" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={`Have any of the following apply to you?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow center className="mt-3">
                <MDBCol md={8}>
                  <strong>
                    1. Indicted, arrested or convicted of any crime other than
                    minor traffic violations during the past ten years?
                  </strong>{" "}
                  ?
                </MDBCol>
                <MDBCol>
                  <MDBFormInline>
                    <MDBInput
                      gap
                      onClick={() => {
                        onChangeField("e", "q1", "Yes");
                        setvalue(value + 1);
                      }}
                      checked={state.q1 == "Yes" ? true : false}
                      label="true"
                      type="radio"
                      id="radio1"
                      containerClass="mr-5"
                    />
                    <MDBInput
                      gap
                      onClick={() => {
                        onChangeField("e", "q1", "No");
                        setvalue(value + 1);
                      }}
                      checked={state.q1 == "No" ? true : false}
                      label="false"
                      type="radio"
                      id="radio2"
                      containerClass="mr-5"
                    />
                  </MDBFormInline>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="mt-3">
                <MDBCol md={8}>
                  <strong>
                    2. The subject of an administrative hearing by any
                    department or Agency of the Fedral Government or any State
                    Government during the past ten (10) years?
                  </strong>{" "}
                </MDBCol>
                <MDBCol>
                  <MDBFormInline>
                    <MDBInput
                      gap
                      onClick={() => {
                        onChangeField("e", "q2", "Yes");
                        setvalue(value + 1);
                      }}
                      checked={state.q2 == "Yes" ? true : false}
                      label="true"
                      type="radio"
                      id="radio1"
                      containerClass="mr-5"
                    />
                    <MDBInput
                      gap
                      onClick={() => {
                        onChangeField("e", "q2", "No");
                        setvalue(value + 1);
                      }}
                      checked={state.q2 == "No" ? true : false}
                      label="false"
                      type="radio"
                      id="radio2"
                      containerClass="mr-5"
                    />
                  </MDBFormInline>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="mt-3">
                <MDBCol md={8}>
                  <strong>
                    3. Has Company, principals, officers or managers filed for
                    Bankruptcy?
                  </strong>{" "}
                  ?
                </MDBCol>
                <MDBCol>
                  <MDBFormInline>
                    <MDBInput
                      gap
                      onClick={() => {
                        onChangeField("e", "q3", "Yes");
                        setvalue(value + 1);
                      }}
                      checked={state.q3 == "Yes" ? true : false}
                      label="true"
                      type="radio"
                      id="radio1"
                      containerClass="mr-5"
                    />
                    <MDBInput
                      gap
                      onClick={() => {
                        onChangeField("e", "q3", "No");
                        setvalue(value + 1);
                      }}
                      checked={state.q3 == "No" ? true : false}
                      label="false"
                      type="radio"
                      id="radio2"
                      containerClass="mr-5"
                    />
                  </MDBFormInline>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {value > 2 && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {!(value > 2) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
